import React from 'react';
import { graphql } from 'gatsby';
import styled from 'styled-components';
import posed from 'react-pose';
import PropTypes from 'prop-types';
import SEO from '../components/seo';

import Card from '../components/PostCard';
import Layout from '../components/layout/Layout';

const ListContainer = posed.div({
  enter: {
    staggerChildren: 50
  },
  exit: { staggerChildren: 20, staggerDirection: -1 }
});

const PosedItem = posed.div({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0, transition: { duration: 200 } }
});
const StyledTitle = styled.h1`
  padding: 2rem 0;
`;

const BlogIndex = ({ data, location }) => {
  const posts = data.allMarkdownRemark.edges;

  return (
    <Layout location={location} theme="dark">
      <SEO title="All posts" keywords={['blog', 'gatsby', 'javascript', 'react']} />
      <div className="container">
        <StyledTitle>The Notes</StyledTitle>

        <ListContainer initialPose="exit" pose="enter">
          <div className="card-group">
            {posts.map(({ node }) => {
              const title = node.frontmatter.title || node.fields.slug;
              return (
                <div className="col-xs-12 col-sm-6" key={node.fields.slug}>
                  <PosedItem key={node.fields.slug}>
                    <Card title={title} date={node.frontmatter.date} link={`/${node.fields.slug}`}>
                      {node.excerpt}
                    </Card>
                  </PosedItem>
                </div>
              );
            })}

          </div>
        </ListContainer>
      </div>
    </Layout>
  );
};
BlogIndex.propTypes = {
  data: PropTypes.shape({
    site: PropTypes.shape({
      siteMetadata: PropTypes.shape({
        title: PropTypes.string.isRequired
      }).isRequired
    }).isRequired,
    allMarkdownRemark: PropTypes.shape({
      edges: PropTypes.arrayOf(PropTypes.shape({
        node: PropTypes.shape({
          title: PropTypes.string,
          date: PropTypes.string
        })
      })).isRequired
    }).isRequired
  }).isRequired,
  location: PropTypes.shape({
    hash: PropTypes.string.isRequired,
    key: PropTypes.string.isRequired,
    pathname: PropTypes.string.isRequired,
    search: PropTypes.string.isRequired
  }).isRequired
};
export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
          }
        }
      }
    }
  }
`;
